import React, { useState } from "react";
import Button from "@mui/material/Button";
import Rating from "@mui/material/Rating";
import StarIcon from "@mui/icons-material/Star";

const RateForm = (props) => {
  let latlon = props.location;
  // state to hold form data
  const [formInfo, setFormInfo] = useState({
    cleanliness: "1",
    reusableMaterial: "1",
    traffic: "1",
    recycle: "1",
    plants: "1",
  });

  const [hover, setHover] = useState(-1);

  // state to change state of different form inputs
  function handleChange(event) {
    const { name, value } = event.target;

    setFormInfo((prev) => {
      if (name === "cleanliness") {
        return {
          ...prev,
          [name]: value,
        };
      } else if (name === "reusableMaterial") {
        return {
          ...prev,
          [name]: value,
        };
      } else if (name === "traffic") {
        return {
          ...prev,
          [name]: value,
        };
      } else if (name === "recycle") {
        return {
          ...prev,
          [name]: value,
        };
      } else if (name === "plants") {
        return {
          ...prev,
          [name]: value,
        };
      }
    });
  }

  //display form info after form is submitted
  function info() {
    console.log(formInfo);
  }

  return (
    <div className="description">
      <form action="../../post" method="post">
        <input
          hidden
          type="text"
          name="location"
          value={JSON.stringify(latlon)}
          readOnly
        />
        <p>
          Cleanliness:
          <Rating
            name="cleanliness"
            className="stars"
            value={formInfo.cleanliness}
            precision={0.5}
            onChange={handleChange}
            onChangeActive={(event, newHover) => {
              setHover(newHover);
            }}
            emptyIcon={
              <StarIcon style={{ opacity: 0.55 }} fontSize="inherit" />
            }
          />
        </p>
        <p>
          Reusable Material:
          <Rating
            name="reusableMaterial"
            className="stars"
            value={formInfo.reusableMaterial}
            precision={0.5}
            onChange={handleChange}
            onChangeActive={(event, newHover) => {
              setHover(newHover);
            }}
            emptyIcon={
              <StarIcon style={{ opacity: 0.55 }} fontSize="inherit" />
            }
          />
        </p>

        <p>
          Traffic:
          <Rating
            name="traffic"
            className="stars"
            value={formInfo.traffic}
            precision={0.5}
            onChange={handleChange}
            onChangeActive={(event, newHover) => {
              setHover(newHover);
            }}
            emptyIcon={
              <StarIcon style={{ opacity: 0.55 }} fontSize="inherit" />
            }
          />
        </p>
        <p>
          Recycle Options:
          <Rating
            name="recycle"
            className="stars"
            value={formInfo.recycle}
            precision={0.5}
            onChange={handleChange}
            onChangeActive={(event, newHover) => {
              setHover(newHover);
            }}
            emptyIcon={
              <StarIcon style={{ opacity: 0.55 }} fontSize="inherit" />
            }
          />
        </p>
        <p>
          Plant Life:
          <Rating
            name="plants"
            className="stars"
            value={formInfo.plants}
            precision={0.5}
            onChange={handleChange}
            onChangeActive={(event, newHover) => {
              setHover(newHover);
            }}
            emptyIcon={
              <StarIcon style={{ opacity: 0.55 }} fontSize="inherit" />
            }
          />
        </p>
        <Button
          variant="contained"
          type="submit"
          onClick={info}
          style={{ float: "right" }}
        >
          Submit
        </Button>
      </form>
    </div>
  );
};

export default RateForm;
